.App {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.header {
  background: rgb(250, 217, 223);
  padding: 20px;
  max-width: 100%;

  h1 {
    text-align: center;
  }
}

.main {
  width: 100%;
  display: flex;
}

.error {
  margin: auto;
  color: red;
}

.control {
  display: flex;
  max-width: 100%;

  select {
    width: 100%;
  }
}

.chart {
  background: rgb(213, 255, 246);
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.map {
  display: flex;
  background: rgb(206, 217, 252);
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.goal,
.year {
  width: 50%;
  margin: 0 5px;
  color:#333;
  text-align: center;
}

.tooltip {
  position: absolute;
  padding: 10px;
  font: 12px Roboto;
  background: rgba(255, 255, 204, 0.9);
  box-shadow: 1px 1px 10px rgba(128, 128, 128, 0.6);
  border: 0px;
  border-radius: 2px;
  pointer-events: none;
}

.states {
  fill: none;
  stroke: #555;
  stroke-linejoin: round;
}

a {
  text-decoration: none;
}